body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(44,44,187);
background: linear-gradient(90deg, rgba(44,44,187,1) 0%, rgba(20,230,44,1) 0%, rgba(0,232,244,1) 0%, rgba(39,152,182,1) 0%, rgba(2,0,36,1) 0%, rgba(45,208,237,1) 0%, rgba(178,252,255,1) 0%, rgba(0,0,0,1) 0%, rgba(33,46,47,1) 50%, rgba(0,0,0,1) 100%, rgba(87,119,187,1) 100%, rgba(6,0,125,1) 100%, rgba(105,213,250,1) 100%, rgba(0,0,0,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  padding: 0%;
  margin: 0%;
}
.App {
  padding-left: 1px;
  padding-right: 1px;
  color: white;
  background: rgb(44, 44, 187);
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  background: linear-gradient(
    90deg,
    rgba(44, 44, 187, 1) 0%,
    rgba(20, 230, 44, 1) 0%,
    rgba(0, 232, 244, 1) 0%,
    rgba(39, 152, 182, 1) 0%,
    rgba(2, 0, 36, 1) 0%,
    rgba(45, 208, 237, 1) 0%,
    rgba(178, 252, 255, 1) 0%,
    rgba(0, 0, 0, 1) 0%,
    rgba(33, 46, 47, 1) 50%,
    rgba(0, 0, 0, 1) 100%,
    rgba(87, 119, 187, 1) 100%,
    rgba(6, 0, 125, 1) 100%,
    rgba(105, 213, 250, 1) 100%,
    rgba(0, 0, 0, 1) 100%
  );
}

body::-webkit-scrollbar {
  display: none;
}

body {
  background-color: rgb(30, 36, 46);
}
.topbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px solid goldenrod;
  height: 50px;
  padding-top: 5px;
  width: 100%;
  padding-right: 10px;
}

.topbar_brand {
  display: flex;
  justify-content: flex-start;
  font-size: 24px;
  text-decoration: none;
  margin-left: 3px;
}
.title {
  padding-left: 3px;
}
.topbar_brand > .navbar-brand {
  color: white;
  font-weight: 500;
}
.search {
  padding-top: 4px;
}
.search > i {
  color: white;
}
.search > input {
  width: 450px;
  padding-left: 5px;
  border: none;
  border-bottom: 1px solid white;
  color: white;
  outline: none;
  background-color: transparent;
}
.search > input:-ms-input-placeholder {
  color: white;
}
.search > input::placeholder {
  color: white;
}
.nav-items > ul {
  display: flex;
  flex-direction: row;
  padding-top: 5px;
}
.nav-items > ul > li {
  display: flex;
  margin-right: 15px;
  cursor: pointer;
}
.nav-items ul .chat {
  margin-right: 0px;
  padding-left: 3px;
}
.nav-items > ul > li > i {
  font-size: 24px;
}
.searchnav {
  display: none;
}
.searchnav > i {
  display: none;
}

.logout ul {
  display: none;
}
.logout:hover ul {
  display: block;
}
.searchusers {
  width: 50%;
  background-color: transparent;
  border: 1px solid goldenrod;
  padding-top: 10px;
  border-radius: 5px;
}

.searchusers ul li {
  list-style: none;
  text-decoration: none;
}
.searchusers ul li a {
  color: white;
}
@media (max-width: 800px) {
  .search > input {
    width: 250px;
  }
}

@media (max-width: 600px) {
  .search {
    display: none;
  }
  .add > i,
  .home > i,
  .addpost > i,
  .chat > i,
  .logout > i {
    display: none;
  }
  .searchnav > i {
    display: block;
  }
  .nav-items > ul > li {
    margin-right: 0px;
  }
  .nav-items .itemcontents .searchnav {
    margin-right: 5px;
  }
  .title {
    font-size: 20px;
  }
}

.leftsidebar {
  padding: 5%;
  height: 92vh;
  overflow-y: scroll;
}
.leftsidebar::-webkit-scrollbar {
  display: none;
}
.hrline {
  /* border: 1px solid white; */
  border-bottom: 1px solid white;
}
.club {
  /* background-color: rgb(172, 96, 243); */
  background-color: transparent;
  padding: 5%;
  border: 1px solid goldenrod;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
}
.club .moreinfobtn {
  background-color: black;
  border: 1px solid goldenrod;
  padding: 5px;
  border-radius: 25px;
  box-shadow: inset 0 0 10px #000000;
}
.club small a {
  color: white;
  text-decoration: none;
  /* font-weight: bold; */
}

@media (max-width: 600px) {
  .clubs {
    margin-bottom: 20%;
  }
}

.post {
  border: 1px solid gold;
  height: -webkit-max-content;
  height: max-content;
  border-radius: 10px;
  object-fit: contain;
  padding: 2%;
  margin-top: 5px;
  margin-bottom: 5px;
  /* background-color: rgb(172, 96, 243); */
  background-color: transparent;
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
}

.userinfo {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  grid-gap: 5px;
  gap: 5px;
}
.userimg > img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
.post .headerpart {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.hideContent {
  overflow: hidden;
  line-height: 1em;
  height: 2em;
}

.showContent {
  line-height: 1em;
  height: auto;
}
.comments a {
  color: white;
  text-decoration: none;
}
.show-more {
  cursor: pointer;
}
.postimg {
  width: 100%;
  background-color: black;
  height: 100%;
}
.postimg iframe {
  max-width: 100%;
  max-height: 600px;
}
.postedimg {
  max-width: 500px;
  max-height: 500px;
  width: 100%;
  display: flex;
  justify-content: center;
  /* display: none; */
}

.postreactions {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 20px;
}

.likes {
  padding-left: 5%;
  cursor: pointer;
}
.share {
  padding-right: 5%;
  cursor: pointer;
}
.likes > small,
.comments > small,
.share > small {
  padding-left: 5px;
}

.post_username {
  cursor: pointer;
  text-decoration: none;
  color: white;
}
.post_username:hover {
  color: white;
}
.rightcontents {
  display: flex;
  grid-column-gap: 5px;
  column-gap: 5px;
}
.rightcontents i {
  cursor: pointer;
}
@media (max-width: 600px) {
  .post {
    margin-bottom: -30px;
    border-left: none;
    border-right: none;
    border-radius: 0%;
  }
  .postedimg {
    height: 50%;
    width: 100%;
  }
  .allposts {
    margin-bottom: 15%;
  }
  .showContent {
    width: 100%;
  }
  .tags {
    font-size: 12px;
  }
}

